::-webkit-scrollbar {
    display: none;
}

h1 {
    margin: 0;
    padding: 0;
}

.TaskManagerPage {
    height: 700px;
    width: 1100px;
    margin: 0 auto;
}

.TaskManagerDisplay {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.5);
}

.taskList {
    width: 250px;
    height: 100%;
    padding: 25px 30px;
    background-color: rgb(238, 238, 238);
    border-radius: 20px 0 0 20px;
    display: flex;
    flex-direction: column;
}

.taskList h1 {
    flex: 0 0 auto;
}

.list {
    height: 1%;
    /* ??? */
    margin: 30px 0;
    flex: 1 0 auto;
    overflow-y: scroll;
}

.list a, .task {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 40px;
    width: 100%;
    background-color: rgba(34, 34, 255, 0.7);
    padding-left: 10px;
    font-size: 18px;
    color: white;
    border-radius: 6px;
    text-decoration: none;
    margin-bottom: 10px;
}

.tasks {
    width: 850px;
    height: 100%;
    padding: 25px 30px;
    background-color: rgb(216, 216, 216);
    border-radius: 0 20px 20px 0;
    position: relative;
}

.tasks h1, .taskList h1 {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Chrome/Safari/Opera */
    -khtml-user-select: none;
    /* Konqueror */
    -moz-user-select: none;
    /* Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
}

.taskButtons {
    display: none;
    margin-right: 10px;
}

.task:hover .taskButtons {
    display: block;
}

.taskButtons img {
    height: 20px;
}

.taskBtn.activeLink {
    background-color: rgb(54, 54, 230);
}

.newListBtn {
    flex: 0 0 auto;
    width: 100%;
    height: 40px;
    border-radius: 6px;
    /* margin-bottom: 20px; */
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(34, 34, 255, 0.7);
    box-shadow: none;
    border: none;
    color: white;
    transition: box-shadow .2s;
    transition: background-color linear .1s;
}

.newListBtn:hover {
    box-shadow: 0 0 3px black;
    background-color: rgb(54, 54, 230);
}

.btnNewList a {
    font-size: 18px;
    text-decoration: none;
    color: white;
}

.btnNewTask {
    width: 40px;
    height: 40px;
    /* border-radius: 50%; */
    /* background-color: red; */
    position: absolute;
    right: 0;
    bottom: 0;
    margin-bottom: 20px;
    margin-right: 20px;
}

.btnNewTask a {
    text-decoration: none;
    font-size: 20px;
    color: white;
}

.newTaskBtn a {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    /* margin-bottom: 20px; */
    font-size: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(34, 34, 255, 0.7);
    box-shadow: none;
    border: none;
    color: white;
    transition: box-shadow .2s;
    transition: background-color linear .1s;
}

.newTaskBtn a:hover {
    box-shadow: 0 0 3px black;
    background-color: rgb(54, 54, 230);
}

.newTaskBtn img {
    width: 24px;
}

.listTasks {
    margin: 30px 0;
}